import { GiMailedFist, GiPocketBow, GiHeartPlus, GiCheckedShield, GiBootPrints, GiFlyingFlag, GiDeathSkull, GiSnakeSpiral } from 'react-icons/gi';
import { useState, useEffect } from 'react';
import { specialRules } from '../databases/ArmyRules';
const iconStyle = { marginBottom: '0.2em' };

const Calculator = () => {
  const [calculatedUnit, setCalculatedUnit] = useState({
    name: 'Custom Unit',
    supertype: 'unit',
    type: 'Infantry',
    move: '–',
    attacks: '–',
    shots: '–',
    hits: '–',
    armour: '–',
    size: 3,
    points: 0,
    rules: '–',
  });

  const handleChange = () => {
    const newName = document.querySelector('#unitName').value;
    const newType = document.querySelector('#unitType').value;
    const attacksIndex = document.querySelector('#unitAttacks').selectedIndex;
    const newAttacks = document.querySelector('#unitAttacks').options[attacksIndex].innerHTML;
    const shotsIndex = document.querySelector('#unitShots').selectedIndex;
    const newShots = document.querySelector('#unitShots').options[shotsIndex].innerHTML;
    const hitsIndex = document.querySelector('#unitHits').selectedIndex;
    const newHits = document.querySelector('#unitHits').options[hitsIndex].innerHTML;
    const armourIndex = document.querySelector('#unitArmour').selectedIndex;
    const newArmour = document.querySelector('#unitArmour').options[armourIndex].innerHTML;
    const rulesIndex = document.querySelector('#unitRules').selectedIndex;
    const newRules = document.querySelector('#unitRules').options[rulesIndex].innerHTML;
    let newSize = 3;
    const calculatePoints = () => {
      let totalPoints = 0;
      totalPoints += parseInt(document.querySelector('#unitAttacks').value) + parseInt(document.querySelector('#unitShots').value) + parseInt(document.querySelector('#unitHits').value) + parseInt(document.querySelector('#unitArmour').value) + parseInt(document.querySelector('#unitRules').value);
      if (newType === 'Artillery') {
        newSize = 2; // ARTILLERY TAX
      } else if (newType === 'Multi-Monster') {
        totalPoints += 10; // MONSTER TAX
      } else if (newType === 'Single-Monster') {
        totalPoints += 10; // MONSTER TAX
        newSize = 1; // SINGLE-MONSTER TAX
      } else if (newType === 'Cavalry') {
        totalPoints += 35; // CAVALRY TAX
      } else if (newType === 'Chariot') {
        totalPoints += 35; // CHARIOT TAX
      }
      let redTax = 0;
      let cliffordTax = 0;
      if (newType !== 'Single-Monster') {
        redTax += parseInt(document.querySelector('#unitAttacks').value) > 35 ? 1 : 0;
        redTax += parseInt(document.querySelector('#unitHits').value) > 5 ? 1 : 0;
        redTax += parseInt(document.querySelector('#unitArmour').value) > 5 ? 1 : 0;
        cliffordTax += parseInt(document.querySelector('#unitAttacks').value) > 50 ? 1 : 0;
        cliffordTax += parseInt(document.querySelector('#unitHits').value) > 5 ? 1 : 0;
        cliffordTax += parseInt(document.querySelector('#unitArmour').value) > 20 ? 1 : 0;
      } else {
        redTax += parseInt(document.querySelector('#unitAttacks').value) > 5 ? 1 : 0;
        redTax += parseInt(document.querySelector('#unitHits').value) > 0 ? 1 : 0;
        redTax += parseInt(document.querySelector('#unitArmour').value) > 5 ? 1 : 0;
        cliffordTax += parseInt(document.querySelector('#unitAttacks').value) > 10 ? 1 : 0;
        cliffordTax += parseInt(document.querySelector('#unitHits').value) > 5 ? 1 : 0;
        cliffordTax += parseInt(document.querySelector('#unitArmour').value) > 35 ? 1 : 0;
      }
      if (redTax === 2) {
        totalPoints += 25; // DOUBLE TAX
      } else if (redTax === 3) {
        totalPoints += 45; // TRIPLE TAX
      }
      if (cliffordTax > 1) {
        totalPoints += 20; // CLIFFORD TAX
        if (newType === 'Single-Monster') {
          totalPoints -= 20; // CLIFFORD REBATE FOR BIG DOGS
        }
      }
      if (redTax > 1 && newType === 'Infantry') {
        totalPoints -= 25; // INFANTRY REBATE
      }
      if (newType === 'Cavalry') {
        if (newArmour === '–' || newArmour === '6+' || newArmour === '5+') {
          totalPoints -= 15; // LIGHT CAVALRY REBATE
        }
      }
      return totalPoints;
    };
    const newPoints = calculatePoints();
    setCalculatedUnit((prev) => ({
      ...prev,
      name: newName ? newName : 'Custom Unit',
      type: newType,
      attacks: newAttacks,
      shots: newShots,
      hits: newHits,
      armour: newArmour,
      size: newSize,
      points: newPoints,
      rules: newRules,
    }));
  };

  const handleReset = () => {
    document.querySelector('#unitAttacks').selectedIndex = 0;
    document.querySelector('#unitShots').selectedIndex = 0;
    document.querySelector('#unitHits').selectedIndex = 0;
    document.querySelector('#unitArmour').selectedIndex = 0;
    document.querySelector('#unitRules').selectedIndex = 0;
    handleChange();
  };

  return (
    <>
      <div className='input-group mb-3'>
        <span className='input-group-text col-3 bg-light'>Name</span>
        <input type='text' className='form-control' placeholder='Custom Unit' id='unitName' onChange={() => handleChange()} />
      </div>
      <div className='input-group mb-3'>
        <span className='input-group-text col-3 bg-light'>Unit Type</span>
        <select className='form-select' id='unitType' onChange={() => handleReset()}>
          <option value='–'>–</option>
          <option value='Infantry'>Infantry</option>
          <option value='Cavalry'>Cavalry</option>
          <option value='Chariot'>Chariot</option>
          <option value='Multi-Monster'>Multi-Monster</option>
          <option value='Single-Monster'>Single-Monster</option>
          <option value='Artillery'>Artillery</option>
        </select>
      </div>
      <div className='input-group mb-3'>
        <span className='input-group-text col-3 bg-light'>Attacks</span>
        <select className='form-select' id='unitAttacks' onChange={() => handleChange()}>
          <option value={0}>–</option>
          {calculatedUnit.type === 'Single-Monster' ? (
            <>
              <option value={5}>1</option>
              <option value={10}>2</option>
              <option value={15}>3</option>
              <option value={20}>4</option>
              <option value={25}>5</option>
              <option value={30}>6</option>
              <option value={35}>7</option>
              <option value={40}>8</option>
              <option value={45}>9</option>
              <option value={50}>10</option>
            </>
          ) : (
            <>
              <option value={5}>1</option>
              <option value={20}>2</option>
              <option value={35}>3</option>
              <option value={50}>4</option>
              <option value={75}>5</option>
              <option value={125}>6</option>
            </>
          )}
        </select>
      </div>
      <div className='input-group mb-3'>
        <span className='input-group-text col-3 bg-light'>Shots</span>
        <select className='form-select' id='unitShots' onChange={() => handleChange()}>
          <option value={0}>–</option>
          {calculatedUnit.type === 'Artillery' ? (
            <>
              <option value={50}>3 / 40cm</option>
              <option value={60}>2 per stand / 40cm</option>
              <option value={65}>3 / 60cm</option>
              <option value={80}>2 +bounce / 60cm</option>
            </>
          ) : calculatedUnit.type === 'Single-Monster' ? (
            <>
              <option value={5}>2 / 20cm</option>
              <option value={15}>3 / 15cm 360°</option>
              <option value={15}>3 / 30cm</option>
            </>
          ) : (
            <>
              <option value={5}>1 / 15cm</option>
              <option value={15}>1 / 15cm 360°</option>
              <option value={15}>1 / 30cm</option>
              <option value={20}>2 / 15cm</option>
            </>
          )}
        </select>
      </div>
      <div className='input-group mb-3'>
        <span className='input-group-text col-3 bg-light'>Hits</span>
        <select className='form-select' id='unitHits' onChange={() => handleChange()}>
          <option value={0}>–</option>
          {calculatedUnit.type === 'Single-Monster' ? (
            <>
              <option value={-20}>2</option>
              <option value={-5}>4</option>
              <option value={0}>6</option>
              <option value={5}>8</option>
              <option value={20}>10</option>
            </>
          ) : (
            <>
              <option value={0}>2</option>
              <option value={5}>3</option>
              <option value={20}>4</option>
            </>
          )}
        </select>
      </div>
      <div className='input-group mb-3'>
        <span className='input-group-text col-3 bg-light'>Armour</span>
        <select className='form-select' id='unitArmour' onChange={() => handleChange()}>
          <option value={0}>–</option>
          <option value={5}>6+</option>
          <option value={20}>5+</option>
          <option value={35}>4+</option>
          <option value={50}>3+</option>
        </select>
      </div>
      <div className='input-group mb-3'>
        <span className='input-group-text col-3 bg-light'>Rules</span>
        <select className='form-select' id='unitRules' onChange={() => handleChange()}>
          <option value={0}>–</option>
          {calculatedUnit.type === 'Single-Monster' ? (
            <>
              <option value={0}>Undead</option>
              <option value={10}>Immune to Terror</option>
              <option value={20}>Regenerates</option>
              <option value={30}>Flying</option>
              <option value={40}>Terrifying</option>
            </>
          ) : (
            <>
              <option value={0}>Undead</option>
              <option value={10}>Fearless</option>
              <option value={20}>Regenerates</option>
              <option value={20}>Pursuer</option>
              <option value={30}>Flying</option>
              <option value={10}>Sharpshooter</option>
              <option value={10}>Armour Piercing Shots</option>
              <option value={15}>Quick Reload</option>
              <option value={15}>Devastating Charge</option>
              <option value={15}>Devastating Shots</option>
              <option value={-5}>Hungry for Humans</option>
              <option value={-10}>Disorderly</option>
              <option value={-20}>Bloodlust</option>
            </>
          )}
        </select>
      </div>
      <h3 className='text-center lead mt-5'>Your Custom Unit</h3>
      <div className='card border-white mb-2'>
        <div className='card-header bg-transparent d-flex px-0'>
          <div className='px-3 text-left flex-grow-1'>
            {calculatedUnit.name} <small className='text-muted'>/ {calculatedUnit.supertype === 'upgrade' ? '+' + calculatedUnit.points : calculatedUnit.points} pts</small>
          </div>
        </div>
        <div className='list-group list-group-flush'>
          <div className='list-group-item list-group-item-light d-flex'>
            <small className='flex-grow-1'>
              {calculatedUnit.size === 1 ? 'One stand ' + calculatedUnit.type + ' ' + calculatedUnit.supertype + '.' : calculatedUnit.size === 2 ? 'Two stand ' + calculatedUnit.type + ' ' + calculatedUnit.supertype + '.' : calculatedUnit.size === 3 ? 'Three stand ' + calculatedUnit.type + ' ' + calculatedUnit.supertype + '.' : ''}
              {calculatedUnit.upgrades ? calculatedUnit.upgrades.map((f) => (f === 'Any' ? calculatedUnit.upgrades + ' character upgrade.' : calculatedUnit.upgrades + ' upgrade.')) : ''}
            </small>
            {calculatedUnit.unique ? <small>One per army</small> : <small>Max {calculatedUnit.max ? calculatedUnit.max : '∞'} per army</small>}
          </div>
          <li className='list-group-item'>
            <small className='pr-1'>
              (<GiMailedFist style={iconStyle} /> {calculatedUnit.attacks})
            </small>
            {calculatedUnit.shots !== '–' ? (
              <small className='px-1'>
                (<GiPocketBow style={iconStyle} /> {calculatedUnit.shots})
              </small>
            ) : (
              ''
            )}
            <small className='px-1'>
              (<GiHeartPlus style={iconStyle} /> {calculatedUnit.hits})
            </small>
            <small className='px-1'>
              (<GiCheckedShield style={iconStyle} /> {calculatedUnit.armour})
            </small>
          </li>
          {calculatedUnit.hits > 5 ? (
            <li className='list-group-item'>
              <small className='card-text'>
                {specialRules.map((e) =>
                  e.name === 'Hard to Kill' ? (
                    <>
                      <strong>{e.name}.</strong> {e.text}
                    </>
                  ) : (
                    ''
                  )
                )}
              </small>
            </li>
          ) : calculatedUnit.shots === '3 / 60cm' ? (
            <>
              <li className='list-group-item'>
                <small className='card-text'>
                  {specialRules.map((e) =>
                    e.name === 'Calculated Shots' ? (
                      <>
                        <strong>{e.name}.</strong> {e.text}
                      </>
                    ) : (
                      ''
                    )
                  )}
                </small>
              </li>
              <li className='list-group-item'>
                <small className='card-text'>
                  {specialRules.map((e) =>
                    e.name === 'Stone Thrower' ? (
                      <>
                        <strong>{e.name}.</strong> {e.text}
                      </>
                    ) : (
                      ''
                    )
                  )}
                </small>
              </li>
            </>
          ) : calculatedUnit.shots === '2 / 15cm' ? (
            <li className='list-group-item'>
              <small className='card-text'>
                {specialRules.map((e) =>
                  e.name === 'Slow Reload' ? (
                    <>
                      <strong>{e.name}.</strong> {e.text}
                    </>
                  ) : (
                    ''
                  )
                )}
              </small>
            </li>
          ) : calculatedUnit.shots === '2 per stand / 40cm' ? (
            <>
              <li className='list-group-item'>
                <small className='card-text'>
                  {specialRules.map((e) =>
                    e.name === 'Ignores Armour' ? (
                      <>
                        <strong>{e.name}.</strong> {e.text}
                      </>
                    ) : (
                      ''
                    )
                  )}
                </small>
              </li>
              <li className='list-group-item'>
                <small className='card-text'>
                  {specialRules.map((e) =>
                    e.name === 'Bolt Thrower' ? (
                      <>
                        <strong>{e.name}.</strong> {e.text}
                      </>
                    ) : (
                      ''
                    )
                  )}
                </small>
              </li>
            </>
          ) : calculatedUnit.shots === '2 +bounce / 60cm' ? (
            <>
              <li className='list-group-item'>
                <small className='card-text'>
                  {specialRules.map((e) =>
                    e.name === 'Ignores Armour' ? (
                      <>
                        <strong>{e.name}.</strong> {e.text}
                      </>
                    ) : (
                      ''
                    )
                  )}
                </small>
              </li>
              <li className='list-group-item'>
                <small className='card-text'>
                  {specialRules.map((e) =>
                    e.name === 'Weakens Cover' ? (
                      <>
                        <strong>{e.name}.</strong> {e.text}
                      </>
                    ) : (
                      ''
                    )
                  )}
                </small>
              </li>
              <li className='list-group-item'>
                <small className='card-text'>
                  {specialRules.map((e) =>
                    e.name === 'Cannon' ? (
                      <>
                        <strong>{e.name}.</strong> {e.text}
                      </>
                    ) : (
                      ''
                    )
                  )}
                </small>
              </li>
              <li className='list-group-item'>
                <small className='card-text'>
                  {specialRules.map((e) =>
                    e.name === 'Grapeshot' ? (
                      <>
                        <strong>{e.name}.</strong> {e.text}
                      </>
                    ) : (
                      ''
                    )
                  )}
                </small>
              </li>
            </>
          ) : calculatedUnit.rules === '–' ? (
            <li className='list-group-item'>
              <small className='card-text text-muted'>This {calculatedUnit.supertype} has no special rules.</small>
            </li>
          ) : (
            ''
          )}
          {calculatedUnit.rules !== '–'
            ? specialRules.map((e) =>
                calculatedUnit.rules === e.name ? (
                  <li className='list-group-item'>
                    <small className='card-text'>
                      <strong>{e.name}.</strong> {e.text}
                    </small>
                  </li>
                ) : (
                  ''
                )
              )
            : ''}
        </div>
      </div>
    </>
  );
};

export default Calculator;
